import PageLayout from '../components/PageLayout';
import Utils from '../util/Utils';

import '../styles/grid.scss';

function wrapDir(i, length) {
    const negative = i < 0 ? -1 : 1;
    const mod = negative * (i % length);
    if (i < length) {
        return length - i;
    }
    return i;
}

class Grid extends React.Component {
    state = {
        highlight: 0,
        names: Utils.getNames(),
    };
    colCount = Math.ceil(Math.sqrt(this.state.names.length));
    rowCount = Math.ceil(this.state.names.length / this.colCount);
    componentDidMount() {
        Utils.loadVoices();
        window.setInterval(() => {
            const c = this.state.highlight;
            const dir = [c - this.colCount, c-1, c + this.colCount, c+1];
            const randomDir = wrapDir(dir[Math.floor(Math.random() * dir.length)], this.state.names.length);
            console.log('dir',randomDir, dir);
            this.setState({ highlight: randomDir });
        }, 1000);
    }
    render() {
        const { names } = this.state;

        const style = {
            width: 100 / this.colCount + '%',
            height: 100 / this.rowCount + '%'
        };

        return (
            <PageLayout className="grid">
                <div className="page_content">
                    {names.map((n, i) =>
                        <div className="name-wrapper" key={n.value} style={style}>
                            <div className="name" data-highlight={i === this.state.highlight}>{n.value}</div>
                        </div>
                    )}
                </div>
            </PageLayout>
        );
    }
}

export default Grid;